.product {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: space-between;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product__img {
  align-self: center;
  width: 160px;
  height: 134px;
  padding-bottom: 40px;
}

.product__title {
  color: #060b35;
  padding-bottom: 4px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  padding-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__rating {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
  display: flex;
}

.product__rating .product__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.product .prise {
  justify-content: space-between;
  padding-bottom: 16px;
  display: flex;
}

.product .prise__title {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product .prise__subtitle {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product__button {
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
}

.product:hover > .product__button {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars > :not(:first-child) {
  margin-left: 4px;
}

.stars--5 .stars__star, .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

